.input {
  width: 100%;
  height: 6rem;
  padding: 1.9rem 2rem 2rem;
  color: var(--black);
  font-size: 1.6rem;
  background: var(--white);
  border: 2px solid rgba(#004685, 0.2);
  border-radius: var(--radius);

  &::placeholder {
    color: var(--black-gray);
    font-size: 1.6rem;
  }
}
