@charset "UTF-8";
/* Reset and base styles */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

:focus-visible {
  outline-offset: 4px;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

a:focus,
a:active {
  outline: none;
}

a,
img,
span,
input,
button,
ion-icon {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

input,
button {
  font: inherit;
  background: none;
  border: none;
}

input {
  width: 100%;
  outline: none;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

ul,
li {
  padding: 0;
  list-style: none;
}

html {
  /* 10px / 16px(по умолчанию) = 0.625 -> 62.5% = 10px => 1rem */
  font-size: 62.5%;
}

body {
  position: relative;
  overflow-x: hidden;
  color: var(--black);
  font-size: var(--font-size-16);
  font-family: var(--font-family);
  line-height: var(--line-height-24);
  background: var(--white);
}

a {
  color: var(--blue);
}

.container-fluid {
  position: relative;
  margin: 0 auto;
  padding: 0 3.2rem;
}

.container {
  position: relative;
  width: 96rem;
  margin: 0 auto;
}

.text-red {
  color: var(--red);
}

.backdrop,
.backdrop-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100vh;
  background: transparent;
  transition: var(--transition-05);
  pointer-events: none;
}
.backdrop.active,
.backdrop-modal.active {
  z-index: 5;
  background: var(--black_15);
  pointer-events: all;
}

.backdrop-modal {
  z-index: 5;
}
.backdrop-modal.active {
  background: rgba(0, 0, 0, 0.8);
}

:root {
  --red: #D30531;
  --white: #fff;
  --black-gray: #00000073;
  --black: #000;
  --light: #E5EDF3;
  --blue-dark: #00468533;
  --blue: #004685;
  --blue-light: #569DDC;
  --black_15: hsla(0, 0%, 0%, 0.15);
  --font-family: "Open Sans", sans-serif;
  --fw-600: 600;
  --fw-500: 500;
  --fw-400: 400;
  --font-size-12: 1.2rem;
  --font-size-16: 1.6rem;
  --font-size-20: 2rem;
  --font-size-24: 2.4rem;
  --font-size-36: 3.6rem;
  --line-height-16: 1.6rem;
  --line-height-24: 2.4rem;
  --line-height-27: 2.7rem;
  --line-height-30: 3rem;
  --line-height-32: 3.2rem;
  --line-height-48: 4.8rem;
  --box-shadow: 0 8px 16px var(--black_15);
  --decoration-underline: underline;
  --radius: 0.8rem;
  --transition-025: 0.25s ease;
  --transition-05: 0.5s ease;
  --transition-1: 1s ease;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 14.2rem;
  height: 6rem;
  padding: 2.1rem 3.2rem;
  color: var(--white);
  font-size: var(--font-size-24);
  line-height: var(--line-height-32);
  background: var(--red);
  border-radius: var(--radius);
  transform: translateY(0);
  cursor: pointer;
  transition: var(--transition-025);
}
.btn:hover {
  transform: translateY(-2px);
}
@media screen and (max-width: 768px) {
  .btn {
    padding: 1rem 2rem;
    font-size: 1.4rem;
    letter-spacing: 0.08rem;
  }
}
.btn.btn-white {
  color: var(--black);
  font-size: 1.6rem;
  background: var(--white);
}
.btn.btn-fullwidth {
  justify-content: center;
  width: 100%;
}
.btn.btn-rounded {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 7rem;
  height: 7rem;
  padding: 0;
  border-radius: 50%;
}
.btn.btn-outlined {
  color: var(--black);
  background: transparent;
  border: 1px solid var(--blue);
}
.btn.btn-wide {
  width: 34rem;
}
.btn.btn-link {
  border: none;
}
.btn.btn-sm {
  padding: 1.5rem;
  letter-spacing: 0;
}
.btn.btn-big {
  gap: 0.8rem;
  justify-content: center;
  width: 30rem;
  height: 6.8rem;
  padding: 2.3rem 3rem;
  font-size: 1.8rem;
  line-height: 2.2rem;
  letter-spacing: 0.1em;
  border-radius: 0.4rem;
}
@media screen and (max-width: 440px) {
  .btn.btn-big {
    width: 100%;
    padding: 1rem 2rem;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}
.btn.btn-up {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  width: 5.6rem;
  height: 5.6rem;
  padding: 0.3rem;
  background: var(--white);
  border-radius: var(--radius-2);
  box-shadow: var(--shadow-1);
}
.btn.btn-up--small {
  width: 3.6rem;
  height: 3.6rem;
}
.btn.btn-up--small .btn-up__icon {
  width: 1.2rem;
  height: 1.4rem;
}
.btn.btn-up__icon {
  width: 1.8rem;
  height: 2.2rem;
}
.btn.btn-success {
  color: var(--white);
  background: var(--green);
}
.btn.btn-success .btn__icon {
  width: 1.2rem;
}
.btn.btn-success .btn__icon path {
  stroke: var(--white);
}

.selected-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 6rem;
  height: 6rem;
  font-family: Bebas-Bold, sans-serif;
  background: var(--white);
  border-radius: var(--radius-2);
  cursor: pointer;
}
.selected-button.is--selected {
  color: var(--white);
  background: var(--green);
}

.cheeps-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 7rem;
  height: 3.2rem;
  padding: 0 1rem;
  color: var(--green);
  font-size: 1.8rem;
  line-height: 2.4rem;
  text-transform: none;
  border-radius: 0.2rem;
  cursor: pointer;
}
.cheeps-btn--blue {
  color: var(--green);
  background: var(--blue);
}
.cheeps-btn--green {
  color: var(--white);
  background: var(--green);
}
.cheeps-btn--darkgreen {
  color: var(--white);
  background: var(--green-dark);
}

.link {
  width: max-content;
  color: var(--black);
}
.link--dotted {
  border-bottom: 2px dotted var(--black);
}
.link--grey {
  color: var(--grey);
}
.link--green {
  color: var(--green);
  border-color: var(--green);
}

.input {
  width: 100%;
  height: 6rem;
  padding: 1.9rem 2rem 2rem;
  color: var(--black);
  font-size: 1.6rem;
  background: var(--white);
  border: 2px solid rgba(0, 70, 133, 0.2);
  border-radius: var(--radius);
}
.input::placeholder {
  color: var(--black-gray);
  font-size: 1.6rem;
}

.tooltip {
  position: absolute;
  top: 6rem;
  right: 0;
  z-index: 6;
  width: 30rem;
  padding: 2rem;
  background: var(--white);
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.33);
  transform: translateY(-5rem);
  visibility: hidden;
  opacity: 0;
  transition: var(--transition-05);
}
.tooltip::before, .tooltip::after {
  position: absolute;
  content: "";
}
.tooltip::before {
  position: absolute;
  top: -0.5rem;
  right: 1rem;
  z-index: 1;
  width: 3rem;
  height: 3rem;
  background: var(--white);
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.33);
  transform: rotate(45deg);
  content: "";
}
.tooltip::after {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  width: 8rem;
  height: 6rem;
  background: var(--white);
  content: "";
}
.tooltip__content {
  position: relative;
  z-index: 3;
}
.tooltip__title {
  font-weight: var(--fw-500);
  font-size: var(--font-size-25);
  line-height: var(--font-size-36);
}
.tooltip__link {
  text-decoration: underline;
}

.modal {
  position: fixed;
  top: 60%;
  left: 50%;
  z-index: 0;
  width: 40rem;
  min-height: 20rem;
  background: var(--white);
  box-shadow: 2px 4px 22px rgba(0, 0, 0, 0.15);
  transform: translate(-50%, -50%);
  visibility: hidden;
  opacity: 0;
  transition: var(--transition-05);
}
.modal.active {
  top: 50%;
  z-index: 10;
  visibility: visible;
  opacity: 1;
  transition: var(--transition-05);
}
.modal__title {
  margin-bottom: 3rem;
}
.modal-inner {
  position: relative;
  padding: 4.5rem 4rem;
}
.modal__close {
  position: absolute;
  top: 1.2rem;
  right: 1.2rem;
  z-index: 5;
  cursor: pointer;
}
.modal__block:not(:last-child) {
  margin-bottom: 2.5rem;
}
.modal__block-text {
  font-size: 1.4rem;
}
.modal__form > * {
  width: 100% !important;
}
.modal__submit {
  margin-top: 3rem;
}
.modal .link {
  color: var(--blue);
  border-bottom: 1px dotted var(--blue);
}
.modal .link:not(:last-child) {
  margin-bottom: 1.7rem;
}

.main {
  padding-top: 6rem;
}

.burger-btn {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 3rem;
  height: 2.3rem;
  cursor: pointer;
}
.burger-btn span {
  width: 100%;
  height: 0.3rem;
  background: var(--blue);
}

.header {
  position: relative;
}
.header__inner {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 6.8rem;
  padding: 1.2rem 3.2rem;
  background-color: var(--white);
}
.header__inner.active {
  position: fixed;
  top: -7rem;
  bottom: auto;
  padding: 1.2rem 3.2rem;
  box-shadow: var(--box-shadow);
  transform: translateY(100%);
  transition: var(--transition-2);
}
.header__inner.header-hide {
  box-shadow: none;
  transform: translateY(0);
}

.mobile-navigation {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 6;
  width: 100%;
  max-width: 35rem;
  background: var(--white);
  transform: translateX(-100%);
  visibility: hidden;
  transition: var(--transition-025);
}
.mobile-navigation.active {
  box-shadow: var(--box-shadow);
  transform: translateX(0);
  visibility: visible;
  transition: var(--transition-05);
}
.mobile-navigation__list {
  width: 100%;
}
.mobile-navigation__block {
  display: flex;
  justify-content: space-between;
}
.mobile-navigation__block--top {
  padding: 3rem;
}
.mobile-navigation__block--menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 85vh;
}
.mobile-navigation__img {
  width: 16rem;
  margin-bottom: 3rem;
}
.mobile-navigation__close {
  font-size: 3rem;
  cursor: pointer;
}
.mobile-navigation__link {
  padding: 2rem 3rem;
  color: var(--blue);
  font-size: 1.8rem;
  text-transform: uppercase;
  cursor: pointer;
}
.mobile-navigation__link:hover {
  color: var(--white);
  background: var(--blue);
}
.mobile-navigation__link:active {
  color: var(--blue-light);
}

.section {
  padding: 6rem 0 5.9rem;
}
.section:not(:last-child) {
  border-bottom: 1px solid var(--blue-dark);
}

.section-hero {
  min-height: 36rem;
  padding: 10rem 0 10.6rem;
  color: var(--white);
  background-image: image-set(url("../images/pic_main2x.jpg") 2x, url("../images/pic_main.jpg") 1x);
  background-size: cover;
}
.section-hero__form {
  display: flex;
  gap: 2rem;
  align-items: flex-end;
}
.section-hero__text {
  margin-top: 1.2rem;
}

.form-group {
  position: relative;
  width: 83%;
}
.form__label {
  font-weight: 500;
  font-size: var(--font-size-36);
  line-height: var(--line-height-48);
}
.form__input {
  margin-top: 1.7rem;
}
.form__search-icon {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  width: 2.6rem;
  height: 2.6rem;
  transform: translateX(130%);
  visibility: hidden;
  cursor: pointer;
  opacity: 0;
  transition: var(--transition-05);
}

.section-navigate {
  margin: 6rem 0 2rem;
}
.section-navigate__inner {
  width: 70rem;
  margin: 0 auto;
}
.section-navigate__title {
  margin-bottom: 1.3rem;
  font-weight: 500;
  font-size: var(--font-size-36);
  line-height: var(--line-height-48);
  text-align: center;
}
.section-navigate__title-mobile {
  display: none;
}
.section-navigate__list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem 3.5rem;
  justify-content: center;
  font-size: var(--font-size-20);
  line-height: var(--line-height-30);
}
.section-navigate__item {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-bottom: none;
}
.section-navigate__item.is--active .section-navigate__arrow {
  transform: rotateX(180deg);
}
.section-navigate__header {
  display: flex;
  align-items: center;
}
.section-navigate__icon {
  display: none;
  margin-right: 3rem;
}
.section-navigate__arrow {
  position: relative;
  display: none;
  margin-left: auto;
  padding: 2rem;
  color: #fff;
  background: transparent;
  border-radius: 1rem;
  transition: var(--transition-05);
}
.section-navigate__arrow::after {
  position: absolute;
  right: 1rem;
  bottom: 0.7;
  border: 0.7rem solid transparent;
  border-top: 0.7rem solid var(--blue);
  content: "";
}
.section-navigate__content {
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility var(--transition-05);
}
.section-navigate__link {
  color: var(--blue);
  border-bottom: 1px dotted var(--blue);
}
.section-navigate__link:active {
  color: var(--blue-light);
  border-bottom: 1px dotted var(--blue-light);
}
.section-navigate__link.is--disabled {
  color: var(--dark);
  border: none;
}

.section-item:nth-child(even) .section-item__inner .section-item__content {
  order: 1;
}
.section-item__inner {
  display: flex;
  gap: 4rem;
}
.section-item__title {
  margin-bottom: 2.3rem;
  font-weight: var(--fw-500);
  font-size: var(--font-size-24);
  line-height: var(--line-height-32);
}
.section-item__content {
  width: 60rem;
}
.section-item__img {
  width: 32rem;
  height: 32rem;
  aspect-ratio: 1;
}
.section-item__block:not(:last-child) {
  margin-bottom: 3.6rem;
}
.section-item__block-title {
  font-weight: var(--fw-500);
  text-transform: uppercase;
}
.section-item__block-text:not(:last-child) {
  margin-bottom: 1rem;
}

.section-partners__title {
  margin-bottom: 5.4rem;
  font-size: var(--font-size-36);
  line-height: var(--line-height-48);
  text-align: center;
}
.section-partners__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.section-partners__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.section-partners__text {
  width: 15rem;
  margin-top: 1.2rem;
  font-size: var(--font-size-12);
  line-height: var(--line-height-16);
  text-align: center;
}

.footer {
  padding: 4rem 0 4.1rem;
  background: var(--blue-dark);
}
.footer__inner {
  display: flex;
  justify-content: space-between;
}
.footer__icon {
  width: 5.2rem;
}

[data-section] > * {
  transform: translateY(10rem);
  opacity: 0;
  transition: var(--transition-1);
}
[data-section].active > * {
  transform: translateY(0);
  opacity: 1;
}

[data-tooltip] {
  position: relative;
  z-index: 2;
}
[data-tooltip]:hover .tooltip {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
  transition: var(--transition-05);
}

@media screen and (max-width: 992px) {
  .container,
  .container-fluid {
    width: 100%;
    padding: 0 2rem;
  }
}
@media screen and (max-width: 800px) {
  .modal {
    width: 100%;
    min-height: 100%;
  }
  .container,
  .container-fluid {
    width: 100%;
    padding: 0 2rem;
  }
  .section {
    padding: 3rem 0;
  }
  .burger-btn {
    flex-shrink: 0;
    width: 2.2rem;
    height: 1.7rem;
  }
  .header__inner {
    padding: 1.2rem 2rem;
  }
  .header__logo {
    order: -1;
    margin-right: 10rem;
  }
  .header__logo img {
    max-width: 100%;
  }
  .header__help {
    display: none;
  }
  .form-group {
    width: 100%;
  }
  .form {
    justify-content: center;
  }
  .form__btn {
    display: none;
  }
  .form__search-icon {
    transform: translateX(0);
    visibility: visible;
    opacity: 1;
  }
  .form__label {
    display: block;
    font-size: var(--font-size-20);
    line-height: var(--line-height-27);
    text-align: center;
  }
  .section-hero {
    min-height: 21.5rem;
    padding: 4rem 0;
  }
  .section-hero__text {
    font-size: var(--font-size-12);
    line-height: var(--line-height-16);
  }
  .input {
    height: 4.6rem;
    padding: 1.3rem 1.2rem 2rem;
  }
  .section-navigate {
    margin-top: 3rem;
  }
  .section-navigate__title, .section-navigate__link {
    font-size: var(--font-size-20);
    line-height: var(--line-height-27);
  }
  .section-navigate__title-desctop {
    display: none;
  }
  .section-navigate__title-mobile {
    display: inline;
  }
  .section-navigate__list {
    display: block;
  }
  .section-navigate__item {
    margin-right: -2rem;
    margin-left: -2rem;
    padding: 1rem 2rem;
    border-bottom: 1px solid var(--blue-dark);
    cursor: pointer;
  }
  .section-navigate__item--empty {
    display: none;
  }
  .section-navigate__item.is--active .section-navigate__content {
    display: block;
    width: 100%;
    height: 100%;
    padding: 2rem 0;
    visibility: visible;
    opacity: 1;
    transition: visibility var(--transition-05), opacity var(--transition-05), height var(--transition-05);
  }
  .section-navigate__link {
    display: table;
  }
  .section-navigate__inner {
    width: 100%;
  }
  .section-navigate__header {
    width: 100%;
  }
  .section-navigate__icon {
    display: block;
  }
  .section-navigate__arrow {
    display: block;
  }
  .section-item {
    display: none;
  }
  .section-item.is--active {
    display: block;
  }
  .section-item__content {
    width: 100%;
  }
  .section-item__title {
    margin-bottom: 3rem;
    font-size: var(--font-size-20);
    line-height: var(--line-height-27);
  }
  .section-partners__title {
    margin-bottom: 3rem;
    font-size: var(--font-size-20);
    line-height: var(--line-height-27);
  }
  .section-partners__list {
    flex-wrap: wrap;
    justify-content: center;
  }
  .section-partners__item {
    width: 48%;
  }
  .footer__inner {
    flex-direction: column;
    justify-content: center;
  }
  .footer__block {
    display: flex;
    gap: 1rem;
    justify-content: center;
    margin-bottom: 2rem;
    font-size: var(--font-size-12);
    line-height: var(--line-height-16);
  }
  .footer__icon {
    width: 8rem;
    margin: 0 auto;
  }
}